}

.popup {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  width: 60%; /* Adjust the width as needed */
  max-width: 600px; /* Optional: Set a maximum width */
  background: #fff;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 252, 252, 0.5); /* Semi-transparent overlay */
  z-index: 999; /* Ensure the overlay is below the popup */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Rest of your popup styles go here */
.card-bg-color{
  background-color: rgba(17, 0, 37, 0.595);
}
.dark-modal-bg {
  background-color: #333; /* Set your desired dark background color */
  color: #fff; /* Set text color for dark theme */
  padding: 20px;
  border-radius: 5px;
}
